.row {
  zoom: 1;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
}

.column {
  display: block;
  float: left;
}

.col-1 {
  width: 8.33333%;
}

.col-2 {
  width: 16.66667%;
}

.col-3 {
  width: 25%;
}

.col-4 {
  width: 33.33333%;
}

.col-5 {
  width: 41.66667%;
}

.col-6 {
  width: 50%;
}

.col-7 {
  width: 58.33333%;
}

.col-8 {
  width: 66.66667%;
}

.col-9 {
  width: 75%;
}

.col-10 {
  width: 83.33333%;
}

.col-11 {
  width: 91.66667%;
}

.col-12 {
  width: 100%;
}

.img-fluid {
  max-width: 100%;
  height: auto;
  display: block;
}

html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, main, nav, section, summary {
  display: block;
}

audio, canvas, progress, video {
  display: inline-block;
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden], template {
  display: none;
}

a {
  background: transparent;
}

a:active, a:hover {
  outline: 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b, strong {
  font-weight: 700;
}

dfn {
  font-style: italic;
}

mark {
  background: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -.5em;
}

sub {
  bottom: -.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 1em 40px;
}

hr {
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0;
}

pre {
  overflow: auto;
}

code, kbd, pre, samp {
  font-family: monospace,monospace;
  font-size: 1em;
}

button, input, optgroup, select, textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

button {
  overflow: visible;
}

button, select {
  text-transform: none;
}

button, html input[type="button"],
input[type="reset"], input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled], html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  line-height: normal;
}

input[type="checkbox"], input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="number"]::-webkit-inner-spin-button, input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

input[type="search"] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}

input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em;
}

legend {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
}

optgroup {
  font-weight: 700;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td, th {
  padding: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}

html {
  font-size: 62.5%;
}

html,
body {
  width: 100%;
  height: 100%;
}

body {
  font-family: "Lato", sans-serif;
  color: #1a1a1a;
  background: #ffffff;
  font-size: 1.3rem;
}

body.is-loading {
  overflow: hidden;
  visibility: hidden;
}

#barba-wrapper  {
  min-height: 50vh;
  overflow: visible;
  position: relative;
  z-index: 10;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 40px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .container {
    width: 85%;
    padding: 0 80px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 80%;
  }
}

*[data-prllx] {
  will-change: transform;
}

h1 {
  margin-bottom: 40px;
}

h2 {
  margin-bottom: 20px;
}

h3 {
  margin-bottom: 15px;
}

p {
  margin-bottom: 20px;
}

.wswyg--content > * + * {
  margin-top: 30px;
}

.wswyg--content p {
  margin: 0px;
}

.wswyg--content ul {
  padding-left: 40px;
}

.wswyg--content ul li + li {
  margin-top: 5px;
}

a, button {
  text-decoration: none;
  outline: none;
}

body > header .container {
  display: flex;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
}

body > header .brand {
  margin-right: auto;
}

body > header .toggle__menu {
  margin-left: auto;
  background: none;
  border: none;
  padding: 10px;
}

@media screen and (min-width: 992px) {
  body > header .toggle__menu {
    display: none;
  }
}

body > header .toggle__menu .icon .line {
  display: block;
  height: 3px;
  width: 20px;
  background: #1a1a1a;
}

body > header .toggle__menu .icon .line + .line {
  margin-top: 4px;
}

body > header nav {
  margin-left: auto;
  display: none;
}

@media screen and (min-width: 992px) {
  body > header nav {
    display: block;
  }
}

body > header nav .nav__main  {
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}

body > header nav .nav__main  .nav__item {
  display: inline-block;
}

body > header nav .nav__main  .nav__item + .nav__item {
  margin-left: 40px;
}

body.showMenu > header nav {
  display: block;
}

.legals {
  padding-top: 200px;
  padding-bottom: 100px;
}

.legals .legals-mentions, .legals .credits {
  text-align: left;
}

.legals .legals-mentions .container, .legals .credits .container {
  max-width: 690px;
  margin-left: auto;
  margin-right: auto;
}

.legals .legals-mentions h1, .legals .legals-mentions h2, .legals .legals-mentions h3, .legals .legals-mentions h4, .legals .legals-mentions h5, .legals .credits h1, .legals .credits h2, .legals .credits h3, .legals .credits h4, .legals .credits h5 {
  color: #1a1a1a;
}

.legals .legals-mentions p, .legals .legals-mentions ul, .legals .legals-mentions ol, .legals .credits p, .legals .credits ul, .legals .credits ol {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}

.legals .legals-mentions p.chapo, .legals .legals-mentions ul.chapo, .legals .legals-mentions ol.chapo, .legals .credits p.chapo, .legals .credits ul.chapo, .legals .credits ol.chapo {
  margin-bottom: 60px;
}

@media (max-width: 768px) {
  .legals .legals-mentions p.chapo, .legals .legals-mentions ul.chapo, .legals .legals-mentions ol.chapo, .legals .credits p.chapo, .legals .credits ul.chapo, .legals .credits ol.chapo {
    margin-bottom: 30px;
  }
}

.error-page {
  padding-top: 200px;
  padding-bottom: 100px;
  min-height: 100vh;
  text-align: center;
}

.error-page h1 {
  color: #1a1a1a;
}

.error-page a {
  color: #999999;
}
